.card {
  padding-top: 20px;

  display: grid;
  grid-template-areas:
    "image title"
    "image feature"
    "button button";

  &--more {
    padding-top: 70px;
    padding-bottom: 25px;
    grid-template-areas:
      "title"
      "text"
      "button";
    grid-template-rows: auto auto auto;
    text-align: center;
  }

  @media (min-width: $tablet-width) {
    text-align: center;
    padding: 30px;
    padding-top: 100px;
    background-color: $color-concrete;
    grid-template-areas:
      "image"
      "title"
      "feature"
      "button";
    grid-template-rows: 61px auto auto auto;

    &--more {
      border: 2px solid $color-concrete;
      padding-top: 160px;
      background-color: transparent;
      grid-template-areas:
        "title"
        "text"
        "button";
      grid-template-rows: auto auto auto;
    }
  }
}

.card__link-image {
  grid-area: image;
  justify-self: center;
  align-self: center;

  @media (min-width: $tablet-width) {
    position: relative;
    bottom: 100px;
  }
}

.card__image {
  @media (min-width: $tablet-width) {
    &--chicken {
      width: 161px;
      height: 172px;
    }

    &--chicken-big {
      width: 168px;
      height: 207px;
    }

    &--fish {
      width: 163px;
      height: 174px;
    }

    &--fish-big {
      width: 168px;
      height: 200px;
    }

    &--buckwheat {
      width: 161px;
      height: 171px;
    }

    &--buckwheat-big {
      width: 168px;
      height: 200px;
    }

    &--rice {
      width: 163px;
      height: 174px;
    }
  }

  @media (min-width: $desktop-width) {
    &--chicken-big {
      width: 172px;
      height: 202px;
    }

    &--fish-big {
      width: 172px;
      height: 196px;
    }

    &--buckwheat-big {
      width: 172px;
      height: 196px;
    }
  }
}

.card__link-title {
  text-decoration: none;
  grid-area: title;

  @media (min-width: $tablet-width) {
    justify-self: center;
  }
}

.card__title {
  color: $color-mine-shaft;
  font-family: $font-primary;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  text-transform: uppercase;
  margin-top: 0;

  &--more {
    grid-area: title;
    position: relative;
    text-align: center;
  }

  &--more::after,
  &--more::before {
    content: "";
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $color-alto;
  }

  &--more::before {
    width: 2px;
    height: 30px;
  }

  &--more::after {
    width: 30px;
    height: 2px;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;

    &--more::before {
      height: 60px;
    }

    &--more::after {
      width: 60px;
    }

    &--more::before,
    &--more::after {
      top: -80px;
    }
  }
}

.card__title br {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.card__features-list {
  grid-area: feature;
  margin-top: 0;
  margin-bottom: 22px;

  @media (min-width: $tablet-width) {
    margin-bottom: 33px;
  }
}

.card__features-item {
  display: flex;
  justify-content: space-between;
  padding-top: 5px;
  padding-bottom: 5px;

  @media (min-width: $tablet-width) {
    border-bottom: 1px solid $color-silver;
  }
}

.card__shopping-link {
  text-decoration: none;
  grid-area: button;
  align-self: flex-end;
}

.card__more-button {
  grid-area: button;
  align-self: flex-end;
}

.card__text {
  grid-area: text;
  margin-top: 0;
}
